html, body, #root, .App {height:100%;}

.movie_loading {display:-ms-flexbox;display:flex; -ms-flex-pack:center; justify-content:center; -ms-flex-align:center; align-items:center; height:100%;}
.movie_list {display:-ms-flexbox;display:flex; min-height:100%; padding:40px; background:#ededed; -ms-flex-wrap:wrap; flex-wrap:wrap; -ms-flex-pack:distribute; justify-content:space-around; -ms-flex-line-pack:center; align-content:center; -webkit-box-sizing:border-box; box-sizing:border-box;}
.movie_wrap {display:-ms-flexbox;display:flex; width:45%; padding:0 20px; margin-bottom:50px; background:#fff; -ms-flex-pack:justify; justify-content:space-between; -ms-flex-align:start; align-items:flex-start; -webkit-box-sizing:border-box; box-sizing:border-box; -webkit-box-shadow:3px 5px 10px rgba(0, 0, 0, 0.2); box-shadow:3px 5px 10px rgba(0, 0, 0, 0.2);}
.movie_imgW {position:relative; top:-20px; width:30%; -webkit-box-shadow:3px 10px 12px rgba(0, 0, 0, 0.3); box-shadow:3px 10px 12px rgba(0, 0, 0, 0.3);}
.moviePoster {width:100%; vertical-align:top;}
.movie_txtW {width:65%; padding-bottom:20px; color:#333;}
.movie_tit {margin:20px 0 0; font-size:20px;}
.movie_genres {margin-top:10px; font-size:12px; color:#999;}
.movie_synop {margin-top:15px; font-size:14px; color:#777;}

@media (max-width:767px){
	.movie_wrap {width:100%; -ms-flex-direction:column; flex-direction:column;}
	.movie_imgW {width:100%;}
	.movie_txtW {width:100%;}
	.movie_tit {margin-top:0;}
}


